@font-face {
  font-family: "Cremona";
  src: url(../fonts/CremonaSans.woff);
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: url(../fonts/Lato-Light.woff);
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: url(../fonts/Lato-LightItalic.woff);
  font-weight: 300;
  font-style: italic;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: url(../fonts/Lato-SemiBold.woff);
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
.svg-image-heart, .svg-image-cat {
  background: url("../images/sprites/main.svg") no-repeat;
}

.svg-image-cat {
  background-position: 0 0;
  background-size: 154.1666666667% auto;
  width: 48px;
}

.svg-image-cat:before {
  content: "";
  display: block;
  font-size: 0;
  line-height: 0;
  padding-top: 54.1666666667%;
}

.svg-image-cat-dims {
  width: 48px;
  height: 26px;
}

.svg-image-heart {
  background-position: 100% 0;
  background-size: 284.6153846154% auto;
  width: 26px;
}

.svg-image-heart:before {
  content: "";
  display: block;
  font-size: 0;
  line-height: 0;
  padding-top: 92.3076923077%;
}

.svg-image-heart-dims {
  width: 26px;
  height: 24px;
}

.image-by-height {
  width: auto;
}

.image-by-height:before {
  content: none;
}

.image-by-height > svg {
  height: inherit;
}

.image-by-height > img {
  height: inherit !important;
  vertical-align: top !important;
  margin: 0 !important;
  padding: 0 !important;
  max-width: none !important;
  max-height: none !important;
  border: 0 !important;
  opacity: 0 !important;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
}

body {
  display: flex;
  flex-direction: column;
  background: #FFEEE6;
  font-family: Cremona, Arial, sans-serif;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #1B1B1B;
  overflow-x: hidden;
}

a {
  text-decoration: unset;
}

p {
  font-family: Lato, Arial, sans-serif;
  font-weight: 300;
}

main {
  height: 100%;
  overflow-y: hidden;
}

.container {
  width: 100%;
  max-width: 1320px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 1.25rem;
}
@media (min-width: 62em) {
  .container {
    padding: 0 2.5rem;
  }
}

h1 {
  font-size: 9.375rem;
  line-height: 1.1;
  color: white;
  font-weight: 400;
  position: relative;
  left: 0.625rem;
  margin: 0 0 0.625rem;
}
h1 span {
  display: block;
  margin-left: 13.3125rem;
}
h1 small {
  font-size: 6.25rem;
  line-height: 1.1;
}
@media (max-width: 75em) {
  h1 {
    font-size: 7.5rem;
  }
  h1 span {
    margin-left: 11.25rem;
  }
}
@media (max-width: 61.9375em) {
  h1 {
    font-size: 5.625rem;
    margin: 0 0 2.25rem;
  }
  h1 span {
    margin-left: 6.25rem;
  }
  h1 small {
    font-size: 3.75rem;
  }
}
@media (max-width: 47.9375em) {
  h1 {
    font-size: 4.125rem;
  }
  h1 span {
    margin-left: 5rem;
  }
  h1 small {
    font-size: 3.125rem;
  }
}
@media (max-width: 35.9375em) {
  h1 {
    margin: 0 0 7.5rem;
  }
  h1 span {
    margin-left: 6.25rem;
  }
  h1 small {
    font-size: 2.625rem;
  }
}

h2 {
  font-size: 4.375rem;
  line-height: 1.1;
  color: #FF7A8B;
  margin: 0 0 2.6875rem;
  font-weight: 400;
}
@media (max-width: 47.9375em) {
  h2 {
    font-size: 3rem;
    text-align: center;
  }
}
@media (max-width: 35.9375em) {
  h2 {
    font-size: 2.625rem;
  }
}
@media (max-width: 23.125em) {
  h2 {
    font-size: 2.25rem;
  }
}

p {
  font-size: 1.25rem;
  line-height: 1.2;
  letter-spacing: 0.02em;
}
@media (max-width: 47.9375em) {
  p {
    font-size: 1.125rem;
  }
}
@media (max-width: 23.125em) {
  p {
    font-size: 1rem;
  }
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

ol {
  list-style-type: none;
  counter-reset: list;
  padding: 0;
  margin: 0;
}
ol li {
  position: relative;
}
ol li::before {
  counter-increment: list;
  content: counter(list);
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
  font-size: 1.875rem;
  line-height: 1.1;
  color: #FF7A8B;
}

.btn {
  display: inline-flex;
  font-family: Lato, Arial, sans-serif;
  font-weight: 600;
  font-size: 0.9375rem;
  line-height: 1.2;
  padding: 1.875rem 1.25rem;
  text-transform: uppercase;
  background: #FEC2B8;
  color: #1B1B1B;
  letter-spacing: 0.02em;
  transition: all 0.2s ease;
}
.btn:hover {
  background: #FE8E69;
  color: white;
}

.star {
  width: 3.6875rem;
  height: 3.6875rem;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.star::before, .star::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 0.0625rem;
  background: linear-gradient(90deg, white 10%, transparent 0 18%, white 0 44%, transparent 0 56%, white 0 82%, transparent 0 90%, white 0);
}
.star::after {
  transform: rotate(90deg);
}

.section__content {
  max-width: 24.1875rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.section__info {
  margin: 0 0 0.625rem;
}
.section__alt {
  margin: 0 0 2.25rem;
}

.wait h2, .when h2, .where h2, .clothes h2 {
  position: relative;
  display: inline-block;
}
.wait h2::after, .when h2::after, .where h2::after, .clothes h2::after {
  content: "";
  position: absolute;
  width: 5rem;
  height: 0.0625rem;
  background: #1B1B1B;
  opacity: 0.6;
  bottom: -0.6875rem;
  left: 50%;
  transform: translateX(-50%);
}

header {
  padding: 20px;
}

.main {
  flex-grow: 1;
  overflow-x: hidden;
}

.welcome {
  padding-top: 11.25rem;
  margin: 0 0 15.25rem;
}
@media (max-width: 35.9375em) {
  .welcome {
    padding-top: 10rem;
  }
}
.welcome__wrapper {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.welcome__blob {
  position: absolute;
  width: 93%;
  background: linear-gradient(286.83deg, #FFCF00 7.98%, #FF7A4E 52.34%, #FF697C 92.3%);
  border-radius: 50%;
  top: -4.625rem;
  right: 13%;
  filter: blur(60px);
  transform: matrix(-0.91, 0.42, 0.42, 0.91, 0, 0);
  z-index: -1;
}
.welcome__blob::before {
  content: "";
