.wait {
  margin: 0 0 4.5625rem;
}
.wait__wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.wait h2 {
  color: #FE8E69;
  margin: 0 0 1.6875rem;
}
.wait h2::after {
  bottom: -0.5rem;
}
.wait p {
  max-width: 37.5rem;
  text-align: center;
}

.when {
  margin: 0 0 9.0625rem;
}
.when__wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
}
.when__inner {
  max-width: 64.125rem;
  width: 100%;
  display: grid;
  grid-template-columns: 4fr 6fr;
  column-gap: 2.4375rem;
}
.when__content {
  padding-top: 8.3125rem;
}

.where {
  margin: 0 0 7.3125rem;
}
.where__wrapper {
  width: 100%;
  max-width: 64.1875rem;
  display: grid;
  grid-template-columns: 6fr 4fr;
  column-gap: 2.4375rem;
}
.where__visual {
  order: -1;
}
.where__content {
  padding-top: 1.625rem;
  text-align: center;
}
.where__info {
  width: 100%;
}

.clothes {
  margin: 0 0 7.5rem;
}
.clothes__wrapper {
  display: flex;
  justify-content: center;
  width: 100%;
}
.clothes__inner {
  max-width: 64.1875rem;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.clothes__inner p {
  max-width: 37.5rem;
  text-align: center;
}
.clothes__header {
  margin: 0 0 5.8125rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.clothes__image {
  width: 100%;
  height: 28.4375rem;
  margin: 0 0 3.125rem;
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
  position: relative;
}
.clothes__image img {
  width: 100%;
  object-fit: cover;
  position: absolute;
  max-width: 60.5rem;
  height: unset;
  z-index: -1;
}
.clothes__footer {
  max-width: 50.875rem;
  width: 100%;
}
.clothes__footer p {
  margin: 0 0 1.875rem;
  max-width: 24.1875rem;
  text-align: left;
}

.appeal__header {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: 10.375rem;
}
.appeal__header span {
  display: block;
  background: white;
  border-top-left-radius: 50%;
  border-top-right-radius: 50%;
}
.appeal__body {
  padding: 0.5rem 0 6.625rem;
  background: white;
  display: flex;
  justify-content: center;
  position: relative;
}
.appeal__inner {
  width: 100%;
  max-width: 64.125rem;
}
.appeal__visual {
  position: absolute;
  right: 0;
  top: 45%;
  transform: translateY(-50%);
  width: 24.1875rem;
  height: 25.6875rem;
  background: #FF7A8B;
  opacity: 0.3;
}
.appeal__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  max-width: 50.8125rem;
  column-gap: 2.4375rem;
  row-gap: 3.75rem;
}
.appeal__item {
  max-width: 24.1875rem;
}
.appeal__subtitle {
  font-weight: 600;
  font-size: 1rem;
  line-height: 1.1875;
  text-transform: uppercase;
  font-family: Lato, Arial, sans-serif;
  padding-left: 1.8125rem;
  letter-spacing: 0.02em;
  margin: 0 0 1.25rem;
  min-height: 2.0625rem;
  display: flex;
  align-items: center;
  position: relative;
  width: 25.3125rem;
}
.appeal__subtitle::before {
  content: "";
  position: absolute;
  width: 5rem;
  height: 0.0625rem;
  background: #1B1B1B;
  bottom: -0.6875rem;
  left: 0;
}
.appeal h2 {
  text-align: center;
  color: #FE8E69;
  margin: 0 0 3.75rem;
}
.appeal i {
  color: #FF334D;
}

.program h2 {
  margin: 0 0 0.8125rem;
}
.program__wrapper {
  background: white;
  padding-top: 6.4375rem;
  position: relative;
  overflow: hidden;
  display: flex;
  justify-content: center;
}
.program__wrapper::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 93.75rem;
  border-radius: 50%;
  background: #FFEEE6;
}
.program__inner {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  max-width: 37.5rem;
  width: 100%;
  padding: 0 0 4.5625rem;
}
.program__list {
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: min-content;
  grid-auto-rows: min-content;
  row-gap: 3.25rem;
  max-width: 30.8125rem;
  padding: 2.9375rem 0;
  width: 100%;
  margin-left: auto;
  position: relative;
  z-index: 2;
}
.program__item {
  display: grid;
  grid-template-columns: 5rem 1.5rem auto;
  column-gap: 1.75rem;
  align-items: center;
}
.program__item:first-child .program__heart::before {
  position: absolute;
  content: "";
  top: 0;
  height: 100%;
  background: #1B1B1B;
  opacity: 0.5;
  width: 0.0625rem;
  z-index: -1;
}
.program__time {
  font-size: 1.875rem;
  line-height: 1.1;
  color: #FE8E69;
}
.program__heart {
  width: 1.5rem;
  height: 1.375rem;
  color: #FF7A8B;
  display: flex;
  justify-content: center;
  align-items: center;
}
.program__heart svg {
  width: 100%;
  height: 100%;
}
.program__text {
  font-size: 1rem;
  line-height: 1.1875;
  text-transform: uppercase;
  font-family: Lato, Arial, sans-serif;
  font-weight: 600;
  letter-spacing: 0.02em;
}

.seeyou {
  position: relative;
  z-index: 2;
  padding: 13.1875rem 0 5rem;
}
.seeyou__wrapper {
  padding-left: 6.6875rem;
}
.seeyou h2 {
  color: white;
  font-size: 3.75rem;
  line-height: 1.1;
  margin: 0 0 17.0625rem;
}
.seeyou p {
  font-size: 1rem;
  line-height: 120%;
}
.seeyou__blob {
  position: absolute;
  width: 70.5rem;
  height: 33.375rem;
  top: -1.6875rem;
